.top-section {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 100vh;

    .intro {
        text-align: right;
        width: 5%;
        padding-right: 4rem;
        border-right: 1px solid var(--text-primary);

        h1 {
            font-size: 5rem;
            font-weight: bold;
            line-height: 0.9em;
            margin: 0;
        }

        p {
            font-size: 2rem;
            margin: 0;
        }
    }

    .signature {
        text-align: left;
        width: 50%;
        height: 100%;
        padding-left: 4rem;
        padding-top: 16rem;

        img {
            border: 5px solid var(--text-primary);
            border-radius: 60px;
            // background: #e0e0e0;
            width: 40%;
        }
    }

    .head-tag {
        position: absolute;
        bottom: 4rem;
        margin: 0 auto;
        text-align: center;
        width: 1100px;
        max-width: 65%;
        line-height: 25px;

        .head-tag-desc {
            color: var(--text-primary);
            animation: blink 2s;
        }

        @keyframes blink {

            75%,
            100% {
                opacity: 1;
            }

            0%,
            25%,
            50% {
                opacity: 0;
            }
        }
    }

    .social {
        // position: absolute;
        // bottom: 3rem;
        // margin: 0 auto;
        // text-align: center;

        position: fixed;
        // top: 50%;
        right: 2rem;
        z-index: 200;
        display: inline-flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
}

@media screen and (max-width: 1300px) {
    .top-section {

        .intro {
            width: 50%;

            h1 {
                font-size: 5rem;
            }
        }
    }
}

@media screen and (max-width: 768px) {
    .top-section {
        flex-direction: column;

        .intro {
            text-align: center;
            border-bottom: 1px solid var(--text-primary);
            border-right: none;
            width: auto;
            padding: 0;
            padding-bottom: 2rem;

            h1 {
                font-size: 3rem;
            }
        }

        .signature {
            height: 50%;
            padding: 0;
            padding-top: 2rem;
        }
    }
}

@media only screen and (max-width: 600px) {
    .top-section {
        .signature {
            height: 50%;
            padding: 0;
            padding-top: 2rem;

            img {
                border: 5px solid var(--text-primary);
                border-radius: 60px;
                // background: #e0e0e0;
                width: 100%;
            }
        }

        .head-tag {
            bottom: 2rem;

            .head-tag-desc {
                font-size: 12px;
                line-height: 14px;
            }
        }
    }
}