// Importing fonts
@import url('https://fonts.googleapis.com/css2?family=Varela+Round&display=swap');

// Variables for light theme
:root {
    --bg-primary: #fafafa;
    --text-primary: #000000;
    // --text-primary: #1bff04;
    --text-secondary: #e0e0e0;
    font-size: 16px;
}

// Variables for dark theme
.dark-mode {
    --bg-primary: #2f2f2f;
    --text-primary: #ffffff;
    --text-secondary: #e0e0e0;
}

body {
    padding: 0;
    margin: 0;
    background: var(--bg-primary);
    color: var(--text-primary);
    // font-family: 'Varela Round', sans-serif;
    font-size: 16px;
    // Transition for dark mode
    transition: all 0.2s;
    font-family: 'IBM Plex Mono', 'Courier New', Courier, monospace;
}

input,
textarea,
button {
    font-family: inherit;
}

.body-wrapper {
    width: 1100px;
    max-width: 90%;
    margin: 0 auto;
}

a {
    color: var(--text-primary);
    text-decoration: none;

    &:hover {
        text-decoration: underline;
    }
}

.MuiTooltip-tooltip {
    font-size: 1em !important;
}

::-webkit-scrollbar {
    width: 0.3rem;
}

::-webkit-scrollbar-track {
    background: transparent;
}

::-webkit-scrollbar-thumb {
    background: var(--text-primary);
    border-radius: 0.5rem;
}

::-webkit-scrollbar-thumb:hover {
    background: var(--text-secondary);
}

.backgroud-head {
    height: 100%;
    width: 100%;
    position: fixed;
    top: 0px;
    left: 0px;
    z-index: -1;

    .backgroud-img {
        width: 100%;
        height: 100%;
        background-size: 128px;
        background-repeat: repeat;
        background-image: url('./assets/images/noise.png');
        opacity: 0.06;
        border-radius: 0px;
    }

    .lines {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        height: 100%;
        margin: auto;
        width: 90vw;
        display: flex;
        justify-content: space-between;
        /* Distribute lines evenly */
    }

    .line {
        position: relative;
        width: 1px;
        height: 100%;
        /*background: #ffffff;  /* Line color */
        overflow: hidden;
    }

    .line::after {
        content: '';
        display: block;
        position: absolute;
        height: 15vh;
        width: 100%;
        top: -50%;
        left: 0;
        background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, #ffffff 75%, #ffffff 100%);
        animation: drop 7s 0s infinite;
        animation-fill-mode: forwards;
        animation-timing-function: cubic-bezier(0.4, 0.26, 0, 0.97);
    }

    /* Different colors for each line's pseudo-element */
    .line:nth-child(1)::after {
        background: linear-gradient(to bottom, transparent 0%, #FF4500 75%);
        animation-delay: 0.5s;
    }

    .line:nth-child(2)::after {
        background: linear-gradient(to bottom, transparent 0%, #32CD32 75%);
        animation-delay: 1s;
    }

    .line:nth-child(3)::after {
        background: linear-gradient(to bottom, transparent 0%, #1E90FF 75%);
        animation-delay: 1.5s;
    }

    .line:nth-child(4)::after {
        background: linear-gradient(to bottom, transparent 0%, #FFD700 75%);
        animation-delay: 2s;
    }

    .line:nth-child(5)::after {
        background: linear-gradient(to bottom, transparent 0%, #8A2BE2 75%);
        animation-delay: 2.5s;
    }

    .line:nth-child(6)::after {
        background: linear-gradient(to bottom, transparent 0%, #20B2AA 75%);
        animation-delay: 3s;
    }

    .line:nth-child(7)::after {
        background: linear-gradient(to bottom, transparent 0%, #DC143C 75%);
        animation-delay: 3.5s;
    }

    .line:nth-child(8)::after {
        background: linear-gradient(to bottom, transparent 0%, #00FA9A 75%);
        animation-delay: 4s;
    }

    .line:nth-child(9)::after {
        background: linear-gradient(to bottom, transparent 0%, #FF1493 75%);
        animation-delay: 4.5s;
    }

    .line:nth-child(10)::after {
        background: linear-gradient(to bottom, transparent 0%, #00BFFF 75%);
        animation-delay: 5s;
    }

    @keyframes drop {
        0% {
            top: -50%;
        }

        100% {
            top: 110%;
        }
    }
}