.about-section h4{font-size:2rem}.about-section .programming-icons svg{margin-left:2rem;width:3.5rem;height:3.5rem}.about-section .programming-icons svg:first-child{margin-left:0}.section-wrapper{width:100%}.section-wrapper .section{padding:1rem 5rem;display:block}.section-wrapper .section .heading-sec h2{text-align:center;font-weight:300;line-height:1.25;font-size:2.25rem}.section-wrapper .section .flex-container{display:flex;flex-direction:column;gap:6px;padding:6px;box-sizing:border-box;border:1px solid var(--text-primary);border-radius:30px;background-color:rgba(0,0,0,.05);margin-bottom:1.5rem}.section-wrapper .section .flex-container .custom-flex-box{display:flex;align-items:flex-start;justify-content:space-between;padding-top:.25rem;padding-bottom:.75rem;padding-left:1rem;padding-right:1rem;border-top-left-radius:1.5rem;border-top-right-radius:1.5rem;border-bottom-left-radius:.75rem;border-bottom-right-radius:.75rem;background-color:rgba(0,0,0,.8);color:#fff}.section-wrapper .section .flex-container .custom-flex-box .vertical-flex{display:flex;flex-direction:column}.section-wrapper .section .flex-container .custom-flex-box .text-leading{font-size:1.25rem;line-height:1.25rem}.section-wrapper .section .flex-container .custom-flex-box .custom-text-company{font-size:.75rem;color:#a09b9b}.section-wrapper .section .flex-container .custom-flex-box .responsive-text{visibility:hidden;font-size:10px}.section-wrapper .section .flex-container .custom-flex-box .responsive-hide{display:inline}.section-wrapper .section .flex-container .techStack-list{background-color:rgba(0,0,0,.05);padding-left:.5rem;padding-right:.5rem;border-top-left-radius:.75rem;border-top-right-radius:.75rem;border-bottom-left-radius:1.5rem;border-bottom-right-radius:1.5rem}.section-wrapper .section .flex-container .techStack-list .techStack-list-span{display:flex;margin-left:1px;margin-top:.5rem;margin-bottom:.5rem;align-items:center;gap:.25rem;white-space:nowrap}.section-wrapper .section .flex-container .techStack-list .techStack-list-span .techStack-list-desc{display:flex;padding-left:1.3px;align-items:center;gap:.5rem;flex-wrap:wrap}.section-wrapper .section .flex-container .techStack-list .techStack-list-span .techStack-list-desc .techStack-list-val{background-color:rgba(156,163,175,.1);outline:1px solid currentColor;color:#7a7a7a;border-radius:9px;width:fit-content;display:flex;align-items:center;gap:.75rem;text-transform:capitalize;font-weight:600;padding:.25rem .75rem;box-shadow:inset 0 1px 2px rgba(0,0,0,.05);font-family:"Courier Prime",monospace}.section-wrapper .section .flex-container .techStack-list .res-list{list-style:none;margin-block-start:.5rem;margin-block-end:.5rem;padding-inline-start:1.25rem}.section-wrapper .section .flex-container .techStack-list .res-list li{position:relative;padding-left:1.5em;margin-bottom:.5em}.section-wrapper .section .flex-container .techStack-list .res-list li::before{content:"◉";position:absolute;left:0;top:.15em;font-size:.9em}@media screen and (max-width: 768px){.section-wrapper .section{padding:3rem;flex-direction:column}.section-wrapper .section .heading-sec h2{font-size:1.5rem}.section-wrapper .section .text-leading{font-size:1.25rem;line-height:1.25rem}.section-wrapper .section .responsive-text{visibility:visible !important}.section-wrapper .section .responsive-hide{display:none !important}.section-wrapper .section .techStack-list .techStack-list-span{flex-direction:column;align-items:flex-start;gap:.5rem}.section-wrapper .section .techStack-list .techStack-list-span .techStack-list-desc .techStack-list-val{box-shadow:0 0 0 1px rgba(255,255,255,.05)}}@media(min-width: 1024px){.responsive-text{font-size:.875rem}.heading-sec h2{font-size:1.875rem}}